.img-wrapper {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;

    &--bordered {
        border: 1px solid black;
    }

    img, svg {
        position: absolute;
    }

    &--cover img {
        top: 0%;
        left: 0%;
        height: 100%;
        min-width: 100%;
        max-width: none;
        object-fit: cover;
        object-position: top center;
    }

    &--contain {
        img, svg {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            max-height: 100%;
            object-fit: contain;
            object-position: center;
        }
        
        svg {
            width: 50%;
            height: 50%;
        }
    }
}